@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,700);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* END RESET */

html {
  --color-yellow: #e5d13f;
  --color-yellow-light: #ffff72;
  --color-gray: #30383f;
  --color-gray-dark: #081219;
  --color-white: #ffffff;
  --font-family-main: "Roboto", sans-serif;
  --height-header: 80px;
  --padding-header: 20px;
}

body {
  color: #30383f;
  font-family: "Roboto", sans-serif;
  font-family: var(--font-family-main);
}

.App {
  display: flex;
  flex-direction: column;
  background-color: #e1e2e1;
  min-height: 100vh;
  font-size: calc(10px + 2vmin);
  color: white;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
}

.wrapper {
  margin: 0 auto;
  width: 90%;
}

header {
  display: flex;
  flex-direction: row;
  justify-content: center;
  height: 80px;
  height: var(--height-header);
  padding-top: 20px;
  padding-top: var(--padding-header);
  background-color: #e5d13f;
  color: #081219;
  color: var(--color-gray-dark);
}

header h1 {
  font-size: 24px;
}

.card-container {
  flex-grow: 1;
  margin-top: -40px;
}

.btn-add-container {
  display: flex;
  justify-content: center;
}

footer {
  height: 80px;
  height: var(--height-header);
  background-color: #ffff72;
  background-color: var(--color-yellow-light);
}

@media only screen and (min-width: 769px) {
  .wrapper {
    width: 60%;
  }
}

/* Card shadows source: https://codepen.io/sdthornton/pen/wBZdXq */
.card-task {
  width: 90%;
  height: 40px;
  margin-bottom: 10px;
  padding: 16px;
  background-color: var(--color-white);
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  border-radius: 5px;
  color: var(--color-gray);
  font-size: 14px;
}

.card-task:hover {
  cursor: pointer;
}

.card-task span:first-child {
  margin-right: 10px;
}

.cross-task {
  text-decoration: line-through;
}

.card-task span {
  cursor: pointer;
}

.loader {
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  z-index: 9999;
  display: flex;
  height: 100vh;
  width: 100%;
  justify-content: center;
  align-items: center;
  background: rgba(255, 255, 255, 0.2);
}

.loader img {
  width: 200px;
}

.loader svg path,
.loader svg rect {
  fill: #fff;
}

#last-index {
  opacity: 0;
}

form input {
  outline: none;
  border: none;
}

button {
  display: flex;
  justify-content: center;
  align-content: center;
  /* position: absolute;
  top: -70px;
  right: 18px; */
  width: 50px;
  height: 50px;
  border: none;
  border-radius: 50%;
  background-color: var(--color-gray-dark);
  outline: none;
  cursor: pointer;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  -webkit-transition: 0.2s;
  transition: 0.2s;
  position: relative;
  top: -39px;
  left: 42%;
}

button:hover {
  -webkit-transform: translateY(-1px);
          transform: translateY(-1px);
  box-shadow: none;
}

button span {
  font-size: 32px;
  color: var(--color-white);
}

#error-loading img {
  width: 100%;
}

#error-loading span {
  color: #222;
}

.absolute {
  position: absolute;
  left: 50%;
  top: 30%;
  z-index: 9999;
}

.relative {
  display: flex;
  flex-direction: column;
  position: relative;
  left: -50%;
}

